body{margin:0;font-family:'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden;width:100%;height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{display:flex;width:100%;height:100vh;background-image:linear-gradient(rgba(22,22,22,0.2), #161616),url(/static/media/background_stars.2f654a73.png);background-size:100% 100%;background-size:cover}

.planet{position:absolute;width:115%;height:110%;top:-10%;left:-15%;background-image:linear-gradient(rgba(22,22,22,0.2), #161616),url(/static/media/earth.2d643cc1.png);background-repeat:no-repeat}.planet_animation{opacity:0;visibility:hidden}.ZoomIn{-webkit-animation:animateZoomIn 2000ms linear;animation:animateZoomIn 2000ms linear}@-webkit-keyframes animateZoomIn{0%{-webkit-transform:scale(1);transform:scale(1);visibility:visible;opacity:1}80%{opacity:0}100%{-webkit-transform:scale(10);transform:scale(10);visibility:hidden;opacity:0;width:0%;height:0%}}@keyframes animateZoomIn{0%{-webkit-transform:scale(1);transform:scale(1);visibility:visible;opacity:1}80%{opacity:0}100%{-webkit-transform:scale(10);transform:scale(10);visibility:hidden;opacity:0;width:0%;height:0%}}

header{display:flex;flex-direction:column;justify-content:center;align-items:flex-end;width:100%;text-align:center;-webkit-animation:animationFade 1500ms linear;animation:animationFade 1500ms linear;z-index:9;color:#E7DFDD;padding:10%}header .title{font-size:8rem;font-weight:100}header .subtitle{font-size:1.5rem;font-weight:400}header button{color:#E7DFDD;background-color:#4717F6;border:none;padding:1rem;border-radius:.2rem;opacity:1;cursor:pointer}header button .button-text{display:inline;transition:all 1000ms}header button:hover .button-text{margin-right:10px}@media (max-width: 575.98px){header{height:100vh;padding:0%;align-items:center}header .title{font-size:3rem}header .subtitle{font-size:1.2rem}}

.dashboard{display:flex;width:100%;height:92vh}

.barMenu{display:none;position:fixed;top:2%;left:5%;width:30px;height:auto}.barMenu .bar{width:100%;height:6px;background-color:#A239CA;border-radius:999px;margin-bottom:12%}.menu{display:flex;align-items:center;flex-direction:column;width:20%;height:100%;color:#E7DFDD;padding:2%;background-color:#0E0B16}.menu .continent-container{width:100%;height:80%;overflow-y:scroll;overflow-x:hidden}.menu .continent-container .continent{width:100%;list-style:none;padding:10px}.menu .continent-container .continent h3{background-color:#A239CA;padding:5px;border-radius:2px}.menu .continent-container .continent .country-container{margin-top:2%;padding:15px}.menu .continent-container .continent .country-container .country{list-style:none;cursor:pointer;transition:all 300ms;padding:1px;border-radius:2px}.menu .continent-container .continent .country-container .country:hover{background-color:#4717F6}.menu .to-Top{display:none;justify-content:center;align-items:center;position:fixed;bottom:2%;right:2%;width:30px;height:30px;padding:10px;border-radius:5px;background-color:#A239CA;font-size:2rem;-webkit-animation:fadeShow 1000ms linear;animation:fadeShow 1000ms linear;z-index:999}.menu .closeBar{width:100%;display:none;font-size:2rem;text-align:end}.menu .continent-container::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:5px;background-color:#F5F5F5}.menu .continent-container::-webkit-scrollbar{width:2px;height:2px;background-color:#F5F5F5}.menu .continent-container::-webkit-scrollbar-thumb{border-radius:5px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#888}.menu .active{background-color:#4717F6}.menu .title{font-size:2rem}@media (max-width: 575.98px){.menu{display:none;width:80%;padding:2%;justify-content:flex-start;align-items:flex-start;position:absolute;z-index:999}.menu .title{margin:auto}.menu .continent-container{width:100%;padding:0%}.menu .continent-container .continent{padding:0px}.menu .continent-container .continent h3{padding:5px}.menu .continent-container .continent .country-container{margin-top:0%;padding:0px}.menu .continent-container .continent .country-container .country{margin-left:5%}.menu .to-Top{display:flex}.menu .closeBar{display:block}.barMenu{display:block}.closeNavBar{-webkit-animation:closeNav 300ms linear;animation:closeNav 300ms linear}.openNavBar{-webkit-animation:openNav 300ms linear;animation:openNav 300ms linear}.closedBar{display:none}.openedBar{display:flex}}@media (min-width: 992px){.menu{display:flex}.closedBar{display:flex}}@-webkit-keyframes closeNav{0%{-webkit-transform:translateX(0%);transform:translateX(0%)}100%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@keyframes closeNav{0%{-webkit-transform:translateX(0%);transform:translateX(0%)}100%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@-webkit-keyframes openNav{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes openNav{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@-webkit-keyframes fadeShow{from{opacity:0}to{opacity:1}}@keyframes fadeShow{from{opacity:0}to{opacity:1}}

.Stats{width:98%;margin-top:1%;border-radius:5px;min-height:500px;padding:13px}.Stats .time-footer{display:flex;flex-direction:row;width:100%;margin-top:5%;min-height:125px;max-height:125px;opacity:0.7}.Stats .time-footer .sun-time{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;width:50%}.Stats .time-footer .sun-time .img_cointainer{display:flex;flex-direction:column;align-items:center;justify-content:center;margin-right:2%}.Stats .time-footer .sun-time .img_cointainer img{width:80px;height:80px}.Stats .time-footer .sun-time .img_cointainer span:last-child{margin-top:5px;font-size:1rem}.Stats .time-footer .currentTime{display:flex;flex-direction:row;justify-content:flex-end;align-items:center;width:50%}.Stats .time-footer .currentTime span{font-size:1.2rem;margin-right:10px}.Stats .time_weather{display:flex;flex-direction:row;justify-content:space-between;width:100%;padding:10px}.Stats .time_weather .card_time{width:180px;min-height:250px;padding:10px;border-radius:5px;background-color:#0E0B16;border:1px solid #A239CA;box-shadow:0px 0px 5px #4717F6}.Stats .time_weather .card_time .img_container{display:flex;justify-content:center;width:100%}.Stats .time_weather .card_time .img_container img{width:45%;height:80px}.Stats .time_weather .card_time h2{width:100%;min-height:30px;overflow-x:hidden}.Stats .time_weather .card_time h3:first-letter{text-transform:uppercase}.Stats .time_weather .card_time .max_min_container{display:flex;flex-direction:column;overflow-x:hidden}.Stats .time_weather .card_time .max_min_container span{font-size:0.8rem;margin-bottom:10px}@media (max-width: 575.98px){.Stats{width:100%;padding:0px}.Stats .time_weather{flex-direction:column;align-items:center}.Stats .time_weather .card_time{width:200px;margin-bottom:20px}.Stats .time-footer{justify-content:flex-end}.Stats .time-footer .currentTime{justify-content:flex-end}.Stats .time-footer .currentTime span{text-align:end;font-size:1.1rem}}

.Weather{width:80%;height:100%;overflow-x:hidden;overflow-y:scroll;padding:2%;color:#E7DFDD}.Weather .title{font-size:4rem;margin:0%}.Weather .banner{display:flex;width:96%;min-height:20%;background-color:rgba(0,0,0,0.6);padding:2%;overflow-x:scroll;border-radius:10px}.Weather .banner .card{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;list-style:none;width:150px;min-width:150px;height:120px;min-height:120px;background-color:transparent;padding:10px;border-radius:5px;cursor:pointer;transition:all 300ms;margin-right:20px}.Weather .banner .card span{width:100%;overflow:hidden;text-overflow:ellipsis}.Weather .banner .card .zoneTitle{font-size:2rem;margin-bottom:10px}.Weather .banner .card:hover{background-color:#4717F6}.Weather .cities{display:flex;align-items:center;flex-direction:row;width:98%;height:70px;margin-top:1%;padding:10px;border-radius:5px;background-color:rgba(0,0,0,0.6);overflow-x:scroll;overflow-y:hidden}.Weather .cities .cities-card{display:flex;align-items:center;justify-content:center;margin-right:1%;height:100%;padding:5px;border-radius:5px;background-color:transparent;cursor:pointer;transition:all 300ms}.Weather .cities .cities-card span{width:100%;overflow:hidden}.Weather .cities .cities-card:hover{background-color:#4717F6}.Weather .to-Top{display:none;justify-content:center;align-items:center;position:fixed;bottom:1%;right:2%;width:30px;height:30px;padding:10px;border-radius:5px;background-color:#A239CA;font-size:2rem;-webkit-animation:fadeShow 1000ms linear;animation:fadeShow 1000ms linear}.Weather .cities::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:5px;background-color:transparent}.Weather .cities::-webkit-scrollbar{width:2px;height:10px;background-color:transparent}.Weather .cities::-webkit-scrollbar-thumb{border-radius:5px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#888}.Weather .banner::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:5px;background-color:transparent}.Weather .banner::-webkit-scrollbar{width:2px;height:10px;background-color:transparent}.Weather .banner::-webkit-scrollbar-thumb{border-radius:5px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#888}.Weather::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:5px;background-color:#F5F5F5}.Weather::-webkit-scrollbar{width:5px;height:2px;background-color:#F5F5F5}.Weather::-webkit-scrollbar-thumb{border-radius:5px;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#888}.active{background-color:#4717F6 !important}@media (max-width: 575.98px){.Weather{width:100%;padding-top:50px}.Weather .title{font-size:3rem;margin:0%}.Weather .cities{width:100%;padding:0px;margin-top:3%}.Weather .to-Top{display:flex}}

